const name = document.getElementById('name');
const out = document.getElementById('out');
const form = document.getElementById('form');

const casar13 = (input) => {
  let alphabet = [...'abcdefghijklmnopqrstuvwxyz'];
  let newArr = [];
  console.log(newArr);
  console.log(alphabet.length);
  let word = input.toLowerCase().split('');
  console.log(word);
  word.forEach((letter) => {
    if (letter.match(/[0-9]/g)) {
      console.log('find a number');
      newArr.push(letter);
    } else {
      let currIndex = alphabet.indexOf(letter) + 13;
      if (currIndex > 26) {
        currIndex = currIndex - 26;
      }
      newArr.push(alphabet[currIndex]);
    }
  });
  console.log('wordddd', word);
  console.log('new arr', newArr);
  // console.log(word);

  return newArr.join('');
};

form.addEventListener('submit', (e) => {
  e.preventDefault();
  console.log(typeof name.value);

  out.innerHTML = casar13(name.value);
});
